import React, { useState } from 'react';
import Grid from '../Grid';
import Container, { BoxContainer } from '../Container';
import Title from '../Title';
import { CenteredText } from '../Text';

import ThreeGrid from './ThreeGrid';
import PokemonInput from './PokemonInput';
import NatureInput from './NatureInput';
import LevelInput from './LevelInput';
import DynamaxButton from './DynamaxButton';
import StatInput from './StatInput';
import IVResult from './IVResult';

const DEV_MODE = (
  !process.env.NODE_ENV ||
  process.env.NODE_ENV === 'development'
);

const IVChecker = () => {
  const [ dynamax, setDynamax ] = useState(false);
  const [ level, setLevel ] = useState(DEV_MODE? 1 : null);
  const [ pokemon, setPokemon ] = useState(DEV_MODE? "" : null);
  const [ nature, setNature ] = useState(DEV_MODE? "" : null);
  const [ pokemonStat, setPokemonStat ] = useState({});

  return (
    <Container id="iv-checker">
      <BoxContainer>
        <Title> </Title>
        <div>
          <PokemonInput
            pokemon={pokemon}
            onChange={setPokemon}
          />
          <Grid column="3:2" gap="1rem">
            <NatureInput
              nature={nature}
              onChange={setNature}
            />
            <LevelInput
              level={level}
              onChange={setLevel}
            />
          </Grid>
          <DynamaxButton
            dynamax={dynamax}
            onChange={setDynamax}
          />
          <ThreeGrid>
            <div />
            <CenteredText></CenteredText>
            <CenteredText></CenteredText>
          </ThreeGrid>
          <StatInput
            stat={pokemonStat}
            onChange={setPokemonStat}
          />
        </div>
      </BoxContainer>
      <BoxContainer>
        <Title></Title>
        <IVResult
          pokemon={pokemon}
          nature={nature}
          stat={pokemonStat}
          dynamax={dynamax}
          level={level}
        />
      </BoxContainer>
    </Container>
  );
};

export default IVChecker;
